<template>
  <div id="app">
    <demonstration
        tag="h2"
        anchor="rate-ping-fen"
        title="Rate "
        :show-content="false">
      <template slot="description">
         <a href="http://element.eleme.io/#/zh-CN/component/rate#rate-ping-fen" target="_blank">Element Rate </a>
      </template>
    </demonstration>
    <demonstration
        anchor="ji-chu-yong-fa"
        title=""
        :highlight="sourcecodeA"
        :multiple="true">
      <template slot="source">
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate v-model="valueA1"></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate
              v-model="valueA2"
              :colors="colorsA2">
          </vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">px</span>
          <vm-rate
              v-model="valueA3"
              :size="36">
          </vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">rem</span>
          <vm-rate
              v-model="valueA4"
              size="2rem">
          </vm-rate>
        </div>
      </template>
      <template slot="explanation">
         <code>colors</code>
         <code>low-threshold</code>
         <code>high-threshold</code> <i class="new"><code>low-threshold</code>  <code>high-threshold</code> 
        <code>colors</code>  <code>iconClasses</code></i>
      </template>
    </demonstration>
    <demonstration
        anchor="fu-zhu-wen-zi"
        title=""
        description=""
        :highlight="sourcecodeB"
        :multiple="true">
      <template slot="source">
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate
              v-model="valueB1"
              show-text
              :texts="['', '', '', '', '']"></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"> <i class="new">()</i></span>
          <vm-rate
              v-model="valueB2"
              :show-text="true"
              text-type="number"
              text-template="{value}"></vm-rate>
        </div>
      </template>
      <template slot="explanation">
         <code>show-text</code>  <code>texts</code> <code>texts</code>
         <code>max</code><i class="new"> <code>text-type="number"</code>  <code>text-template="{value}"</code>
        </i>
      </template>
    </demonstration>
    <demonstration
        anchor="qi-ta-icon"
        title=" icon"
        description=" icon "
        :highlight="sourcecodeC">
      <template slot="source">
        <vm-rate
            v-model="valueC1"
            :iconClasses="['vm-icon-sad', 'vm-icon-neutral-full', 'vm-icon-smile']"
            voidIconClass="vm-icon-neutral-empty"
            disabledVoidIconClass="vm-icon-neutral-full"
            :colors="['#99A9BF', '#F7BA2A', '#FF9900']"></vm-rate>
      </template>
      <template slot="explanation">
         <code>icon-classes</code>  3  <code>void-icon-class</code> <i
          class="new"> <code>disabledVoidIconClass</code>
        </i>
      </template>
    </demonstration>
    <demonstration
        anchor="zhi-du"
        title=""
        description=""
        :highlight="sourcecodeD"
        :multiple="true">
      <template slot="source">
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"> <i class="new">()</i></span>
          <vm-rate
              v-model="valueD1"
              disabled
              :allow-half="true"
              show-text
              text-color="#ff9900"
              text-template="{value}"></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"> <i class="new">()</i></span>
          <vm-rate
              v-model="valueD1"
              disabled
              :allow-half="false"
              show-text
              text-color="#ff9900"
              text-template="{value}"></vm-rate>
        </div>
      </template>
      <template slot="explanation">
         <code>disabled</code>
         <code>show-text</code> <code>text-template</code>
         <code>{value}</code>
        <code>{value}</code> <i class="new"> <code>allow-half="true"</code> 
        <code>text-template="{value}"</code></i>
      </template>
    </demonstration>
    <demonstration
        anchor="ban-xing"
        :highlight="sourcecodeD"
        :multiple="true">
      <template slot="title">
         <i class="new">()</i>
      </template>
      <template slot="description">
         .5 
      </template>
      <template slot="source">
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate
              v-model="valueE1"
              :allow-half="true"></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate
              v-model="valueE2"
              :allow-half="true"
              show-text></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration"></span>
          <vm-rate
              v-model="valueE3"
              :allow-half="true"
              show-text
              text-type="number"
              text-color="#ff9900"
              text-template="{value}"></vm-rate>
        </div>
        <div class="vd-demo__block vd-demo__block-1">
          <span class="vd-demo__demonstration">10</span>
          <vm-rate
              v-model="valueE4"
              :max="10"
              :allow-half="true"
              show-text></vm-rate>
        </div>
      </template>
      <template slot="explanation">
         <code>allow-half</code>  <code>show-text</code> <code>text-type="number"</code>
          <code>text-template</code>  <code>{value}</code>
        <code>{value}</code>  <code>:max="10"</code>
      </template>
    </demonstration>
    <demonstration
        anchor="attributes"
        title="Attributes"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          </thead>
          <tbody>
          <tr>
            <td>size <i class="vd-demo__new"></i></td>
            <td></td>
            <td>number / string</td>
            <td>% / in / cm / mm / em / ex / pt / pc / px / rem</td>
            <td>18</td>
          </tr>
          <tr>
            <td>max</td>
            <td></td>
            <td>number</td>
            <td></td>
            <td>5</td>
          </tr>
          <tr>
            <td>disabled</td>
            <td></td>
            <td>boolean</td>
            <td></td>
            <td>false</td>
          </tr>
          <tr>
            <td>allow-half</td>
            <td></td>
            <td>boolean</td>
            <td></td>
            <td>false</td>
          </tr>
          <tr>
            <td>low-threshold</td>
            <td></td>
            <td>number</td>
            <td></td>
            <td>2</td>
          </tr>
          <tr>
            <td>high-threshold</td>
            <td></td>
            <td>number</td>
            <td></td>
            <td>4</td>
          </tr>
          <tr>
            <td>colors</td>
            <td>icon  3  3 </td>
            <td>array</td>
            <td></td>
            <td>['#F7BA2A', '#F7BA2A', '#F7BA2A']</td>
          </tr>
          <tr>
            <td>void-color</td>
            <td> icon </td>
            <td>string</td>
            <td></td>
            <td>#C6D1DE</td>
          </tr>
          <tr>
            <td>disabled-void-color</td>
            <td> icon </td>
            <td>string</td>
            <td></td>
            <td>#EFF2F7</td>
          </tr>
          <tr>
            <td>icon-classes</td>
            <td>icon  3  3 </td>
            <td>array</td>
            <td></td>
            <td>['vm-icon-star-on', 'vm-icon-star-on','vm-icon-star-on']</td>
          </tr>
          <tr>
            <td>void-icon-class</td>
            <td> icon </td>
            <td>string</td>
            <td></td>
            <td>vm-icon-star-empty</td>
          </tr>
          <tr>
            <td>disabled-void-icon-class</td>
            <td> icon </td>
            <td>string</td>
            <td></td>
            <td>vm-icon-star-on</td>
          </tr>
          <tr>
            <td>show-text</td>
            <td></td>
            <td>boolean</td>
            <td></td>
            <td>false</td>
          </tr>
          <tr>
            <td>text-color</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td>#1F2D3D</td>
          </tr>
          <tr>
            <td>texts</td>
            <td></td>
            <td>array</td>
            <td></td>
            <td>['', '', '', '', '']</td>
          </tr>
          <tr>
            <td>text-template</td>
            <td></td>
            <td>string</td>
            <td></td>
            <td>{value}</td>
          </tr>
          <tr>
            <td>text-type <i class="vd-demo__new"></i></td>
            <td></td>
            <td>string</td>
            <td>number</td>
            <td>text</td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
    <demonstration
        anchor="event"
        title="Events"
        :show-content="false">
      <template slot="table">
        <table class="vd-demo__table">
          <thead>
          <th></th>
          <th></th>
          <th></th>
          </thead>
          <tbody>
          <tr>
            <td>change</td>
            <td></td>
            <td></td>
          </tr>
          </tbody>
        </table>
      </template>
    </demonstration>
  </div>
</template>
<script>
  import { sourcecodeA, sourcecodeB, sourcecodeC, sourcecodeD } from './template.js'

  export default {
    data () {
      return {
        valueA1: 0,
        valueA2: 0,
        valueA3: 0,
        valueA4: 0,
        colorsA2: ['#99A9BF', '#F7BA2A', '#FF9900'],
        valueB1: 3,
        valueB2: 3,
        valueC1: 0,
        valueD1: 3.7,
        valueD2: 3.7,
        valueE1: 0,
        valueE2: 0,
        valueE3: 0,
        valueE4: 0,
        sourcecodeA,
        sourcecodeB,
        sourcecodeC,
        sourcecodeD
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  #app {
    margin: 60px auto;
  }
</style>



